
import React, { useEffect, useRef, useState } from 'react'
import Select from 'react-select';
import makeAnimated from 'react-select/animated';

const options = [
   { value: 'On wheels', label: 'On wheels' },
   { value: 'Box game', label: 'Box game' },
   { value: 'Art', label: 'Art' },
   { value: 'Baby', label: 'Baby' },
   { value: 'Doll', label: 'Doll' },
   { value: 'Puzzle', label: 'Puzzle' },
   { value: 'Outdoor', label: 'Outdoor' },
];

const animatedComponents = makeAnimated();

// export class ToyFilter extends React.Component {
export function ToyFilter({ currFilter, onFilterBy }) {

   const [filterBy, setFilterBy] = useState({ stock: '', sort: '', name: '', selectedOption: [] })

   // const isInit = useRef()

   useEffect(() => {
      // console.log('currFilter', currFilter);
      if (!currFilter) return
      setFilterBy(currFilter)
   }, [])

   // USE BUILDED HOOT THAT NOT START ON THE FIRST LOAD
   // AND HOW CAN SOLVE NOT SENDING REQ ON EVERY LETTER. DEBOUNCE??
   useEffect(() => {
      // console.log('useEffect');
      onSubmit()
   }, [filterBy])


   const handlerChange = ({ target }) => {
      let { name, value } = target
      setFilterBy((prevState) => ({ ...prevState, [name]: value }))
   }

   const handlerChangeSelect = (labels) => {
      setFilterBy((prevState) => ({ ...prevState, selectedOption: labels }))
   }

   const onSubmit = (ev) => {
      !!ev && ev.preventDefault()
      onFilterBy(filterBy)
   }

   return (
      <section>
         <form onSubmit={onSubmit} className="toy-filter-form">

            <select name="stock" id="stock" value={filterBy.stock} onChange={handlerChange}>
               <option value="">All</option>
               <option value={true}>In stock</option>
               <option value={false}>Out of stock</option>
            </select>

            <input type="search" placeholder="Search toy" name="name" id="name" value={filterBy.name} onChange={handlerChange} />

            <Select
               closeMenuOnSelect={false}
               components={animatedComponents}
               onChange={handlerChangeSelect}
               defaultValue={[options[0], options[1]]}
               isMulti
               options={options}
               value={filterBy.selectedOption}
            />

            <select name="sort" id="sort" value={filterBy.sort} onChange={handlerChange}>
               <option value="Newest" >Date: Newest</option>
               <option value="Oldest">Date: Oldest</option>
               <option value="Higher">Price: Higher</option>
               <option value="Lower">Price: Lower</option>
            </select>
         </form>
      </section>
   )
}